.p-voting{
    margin-top: 65px;
    margin-bottom: 30px;
}
.p-voting>h2{
    background-image: linear-gradient(45deg, #0069d9, transparent);
}
.p-voting-info{
    display:flex;
    flex-direction: column;
}
@media (min-width: 992px) {
    .p-voting-info{
        flex-direction: row;
        width: 100%;
        justify-content: space-evenly;
    }
    .p-voting-info>div{
        width: 50%;
        margin:2px;
    }
}